$color-blue: #1EACC7;
$color-red: #FF5656;

html {
	font-family: Roboto;
	font-weight: regular; }

body {
	font-family: Roboto;
	font-weight: regular; }

.container {
	padding: 0; }

.header {
	position: relative;
	&__nav-menu {
		padding: 13px 70px; }
	&__row {
		height: 84px; // задали высоту,чтобы внутри данного блока можно было выровнять эоементы по центру
		display: flex;
		align-items: center; } }

.nav-menu {
	position: fixed;
	width: 100%;
	height: 50px;
	background: #212121;
	z-index: 5;
	list-style-type: none;
	&__list {
		display: flex;
		justify-content: space-between;
		margin-bottom: 0;
		padding: 0;
		list-style-type: none; } // убрали маркеры у маркированного списка
	&__list-item {
		border-right: 1px dashed #000000;
		padding-right: 45px;
		&:last-child {
			border-right: none;
			padding-right: 0; } }
	a {
		font-weight: 300;
		font-size: 15px;
		color: #FFFFFF;
		text-decoration: none; } }

.subheader {
	height: 84px;
	background: rgba(0, 0, 0, .6);
	position: absolute;
	top: 50px;
	width: 100%;
 }	// позиционирование одной секции над другой. Абсолютно спозиционировали, чтобы вырвать данную секцию и наложить ее над main-section (т.е., чтобы она была сверху)
.logo {
	padding: 0;
	&__img {
		max-width: 100%; //задали ширину в 100% от родительского элемента, который занимает 4 колонкм на случай изменения логотипа
		display: block;
		margin-bottom: 6px; }
	&__text {
		font-weight: 300;
		font-size: 13px;
		opacity: 0.5;
		color: #FFFFFF;
		margin: 0; } }

.call {
	padding: 0;
	margin-right: 32px;
	&__text {
		font-weight: 300;
		font-size: 12px;
		line-height: 14px;
		text-align: right;
		opacity: 0.7;
		color: #FFFFFF;
		margin-bottom: 6px; }
	&__number {
		font-weight: 400;
		font-size: 22px;
		text-align: right;
		color: #FFFFFF;
		text-decoration: none;
		display: block;
		text-align: right; } }

.btn {
	padding: 0;
	&__call {
		width: 172px;
		height: 46px;
		background: linear-gradient(0deg, $color-blue 0%, #4CE2FF 100%);
		box-shadow: inset 0px -3px 0px rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		font-weight: 400;
		font-size: 14px;
		text-align: center;
		text-transform: uppercase;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		padding: 0; } }

.main-section {
	min-height: 800px;
	background: url(../img/main_bg.jpg) center center/cover no-repeat;
	padding: 200px 0 186px 0; }

.wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	&__title {
		font-weight: 900;
		font-size: 60px;
		text-transform: uppercase;
		color: #FFFFFF;
		margin-bottom: 22px; }
	&__second-title {
		font-weight: 700;
		font-size: 36px;
		text-transform: uppercase;
		color: #FFFFFF;
		margin-bottom: 44px; }
	&__text {
		font-weight: 400;
		font-size: 18px;
		text-align: center;
		color: #FFFFFF;
		line-height: 24px;
		margin-bottom: 105px; }
	&__btn {
		width: 259px;
		height: 63px;
		background: linear-gradient(0deg, $color-blue 0%, #4CE2FF 100%);
		box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.12);
		border: none;
		border-radius: 4px;
		font-weight: 400;
		font-size: 18px;
		text-align: center;
		text-transform: uppercase;
		color: #FFFFFF;
		text-shadow: 1px 1px 0px rgba(0, 0, 0, 0.21);
		padding: 0; } }

.advantages-section {
	min-height: 850px;
	padding: 42px 23px; }

.title-container {
	display: flex;
	flex-direction: column;
	align-items: center; }

.label {
	width: 112px;
	height: 24px;
	display: flex;
	align-items: center;
	justify-content: center;
	background: #000000;
	font-weight: 400;
	font-size: 11px;
	text-transform: uppercase;
	color: #FFFFFF;
	margin-bottom: 33px;
	&_white {
		background: #FFFFFF;
		color: #070716; } }

.title {
	font-weight: 700;
	font-size: 38px;
	text-align: center;
	text-transform: uppercase;
	color: #222222;
	margin-bottom: 25px;
	&_margin {
		margin-bottom: 5px; }
	&_white {
		color: #FFFFFF; } }

.subtitle {
	font-weight: 400;
	font-size: 17px;
	text-align: center;
	color: #222222;
	margin-bottom: 30px;
	&_margin {
		margin-bottom: 54px; }
	&_white {
		color: #FFFFFF; } }

.advantages-wrapper {
	padding: 0;
	margin-bottom: 26px;
	&:nth-child(n+7) {
		margin-bottom: 0; }
	&__box {
		min-height: 155px;
		display: flex;
		justify-content: center;
		align-items: center; }
	&__circle {
		width: 116px;
		height: 116px;
		border-radius: 100%;
		background: $color-blue;
		display: flex;
		justify-content: center;
		align-items: center;
		margin-right: 25px; }
	&__desc {
		max-width: 371px; }
	&__title {
		font-weight: 400;
		font-size: 17px;
		line-height: 21px;
		color: $color-blue; }
	&__text {
		font-weight: 300;
		font-size: 15px;
		line-height: 21px;
		color: #202020;
		margin: 0; } }

.mobile-section {
	min-height: 864px;
	padding: 48px 0 80px 0;
	background: url(../img/mobile_bg.jpg) center center/cover no-repeat; }

.mobile-wrapper {
	padding: 0;
	margin-bottom: 19px;
	position: relative;
	&:nth-child(n+4) {
		margin-bottom: 0; }
	&__rectangle {
		width: 320px;
		height: 280px;
		margin: 0 auto;
		padding-top: 88px;
		&_1 {
			background: url(../img/mobile/mobile_screen_1.jpg) center center/cover no-repeat; }
		&_2 {
			background: url(../img/mobile/mobile_screen_2.jpg) center center/cover no-repeat; }
		&_3 {
			background: url(../img/mobile/mobile_screen_3.jpg) center center/cover no-repeat; }
		&_4 {
			background: url(../img/mobile/mobile_screen_4.jpg) center center/cover no-repeat; }
		&_5 {
			background: url(../img/mobile/mobile_screen_5.jpg) center center/cover no-repeat; }
		&_6 {
			background: url(../img/mobile/mobile_screen_6.jpg) center center/cover no-repeat; } }
	&__text {
		font-weight: 400;
		font-size: 24px;
		text-align: center;
		text-transform: uppercase;
		color: #FFFFFF; }
	&__plus {
		position: absolute;
		left: 50%; // сместили плюсик влево на 50%, но он встал чуть правее, чем это нужно, так как его ширина полностью входит в оставшиеся 50%, поэтому нужно прменить transform: translateX(-50%)
		transform: translateX(-50%); // сместили икнонку с плюсиком на 50% влево от его ширины (то есть на 13px)
		bottom: 35px;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 26px;
		height: 26px;
		border-radius: 100%;
		background: #000000;
		margin: 0 auto;
		cursor: pointer; } } // при наведении вид курсора будет меняться

.automobile-section {
	min-height: 896px;
	padding: 57px 0 138px 0;
	background: url(../img/automobile_bg.jpg) center center/cover no-repeat;
	&__automobile-container {
		display: flex;
		flex-direction: column;
		align-items: center; } }

.automobile-container {
	&__img {
		width: 759px; }
	&__text {
		font-weight: 300;
		font-size: 17px;
		text-align: center;
		color: #222222;
		margin: 0; } }
